.CreateFinalCard-container {
    margin: 120px 10% 50px 10%;
}

.CreateFinalCard-inputArea {
    display: flex;
    flex: 1;
    padding: 0px 0px 10px 0px;
    justify-content: space-between;
    align-items: center;
    font-size: 18;
    font-weight: 500;
}

.CreateFinalCard-input {
    height: 40px;
    width: 70%;
    background-color: #E8E8E8;
    border-width: 0px;
    border-radius: 8px;

}

.CreateFinalCard-xBtn {
    height: 16px;
    width: 16px;
}

.CreateFinalCard-xBtn {
    cursor: pointer;
}

.CreateFinalCard-buttonArea {
    display: flex;
    flex-direction: column;
}

.CreateFinalCard-button {
    padding: 0px 20px 0px 20px;
    height: 40px;
    margin-top: 40px;
    font-size: 18px;
    background-color: rgba(251, 188, 5, 0.7);
    border-radius: 8px;
    border-width: 0px;
    justify-content: center;
    align-items: center;
    align-self: flex-end;
}

.CreateFinalCard-button:hover {
    background-color: rgba(251, 188, 5, 0.5);
    cursor: pointer;
}