.CreateQuiz-container {
    margin: 120px 10% 50px 10%;
}

.CreateQuiz-inputArea {
    display: flex;
    flex: 1;
    padding: 0px 0px 10px 0px;
    justify-content: space-between;
    align-items: center;
    font-size: 18;
    font-weight: 500;
}

.CreateQuiz-input {
    height: 40px;
    width: 70%;
    background-color: #E8E8E8;
    border-width: 0px;
    border-radius: 8px;
}

.CreateQuiz-questionList {
    display: flex;
    flex-direction: column;
}

.CreateQuiz-questionListTitle {
    align-self: flex-start;
    font-size: 18;
    font-weight: 500;
}

.CreateQuiz-questionArea {
    display: flex;
    flex-direction: row;
    height: 40px;
    margin-top: 18px;
    justify-content: space-between;
    align-items: center;
    padding: 0px 2% 0px 2%;
    border-width: 0px;
    border-radius: 8px;
    background-color: rgba(251, 188, 5, 0.7);
}

.CreateQuiz-iconsArea {
    width: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.CreateQuiz-icon {
    height: 20px;
    width: 20px;
}

.CreateQuiz-icon:hover {
    cursor: pointer;
}

.CreateQuiz-addQuestionButton {
    display: flex;
    height: 40px;
    width: 40px;
    margin-top: 18px;
    justify-content: center;
    align-items: center;
    align-self: flex-end;
    background-color: rgba(251, 188, 5, 0.7);
    background-image: url('../assets/plus.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px 20px;
    border-width: 0px;
    border-radius: 30px;
}

.CreateQuiz-addQuestionButton:hover {
    cursor: pointer;
    background-color: rgba(251, 188, 5, 0.5);
}

.CreateQuiz-finalButtonsArea {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.CreateQuiz-button {
    padding: 0px 14px 0px 14px;
    height: 40px;
    margin-top: 18px;
    font-size: 18px;
    background-color: rgba(251, 188, 5, 0.7);
    border-radius: 8px;
    border-width: 0px;
    justify-content: center;
    align-items: center;
}

.CreateQuiz-button:hover {
    background-color: rgba(251, 188, 5, 0.5);
    cursor: pointer;
}

.CreateQuiz-LoaderArea {
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px 2% 0px 2%;
}

.CreateQuiz-loader {
    border: 4px solid #f3f3f3;
    border-top: 4px solid rgba(251, 188, 5, 0.7);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 10px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  .CreateQuiz-imageArea {
      display: flex;
      flex-direction: row;
      padding: 0px 2% 0px 2%;
      align-items: center;
      justify-content: space-between;
      height: 40px;
      width: 70%;
      background-color: #E8E8E8;
      border-width: 0px;
      border-radius: 8px;
  }

  .CreateQuiz-image { 
      height: 30px;
      transition: .2s;
  }

  .CreateQuiz-image:hover {
      transform: scale(5);
  }

  .CreateQuiz-xBtn {
      height: 16px;
      width: 16px;
  }

  .CreateQuiz-xBtn:hover {
      cursor: pointer;
  }